import React, {useContext} from "react";
import {AccordionContext} from "react-bootstrap";
import Accordion from 'react-bootstrap/Accordion';
import { useAccordionToggle } from 'react-bootstrap/AccordionToggle';

import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import link from "../link.module.scss";
import styles from "./sidebar.module.scss";
import Nav from 'react-bootstrap/Nav';
import { FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import { faRss,faUser, faAngleDown, faAngleRight, faUserCircle } from '@fortawesome/free-solid-svg-icons'
import { faTimesCircle } from '@fortawesome/free-regular-svg-icons';
import {faFacebookF, faTwitter, faYoutube, faInstagram} from '@fortawesome/free-brands-svg-icons';
import {library} from "@fortawesome/fontawesome-svg-core";
import {SidebarNav} from './SidebarItems';
import {SocialBarItems} from './SocialBarItems';

class Sidebar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            sidebarToggleOn: props.sidebarToggleOn,
            //sidebarToggleOnPrivate: props.sidebarToggleOn?true:false,
            collapsed: false,
            userInfoCollapsed:true
        };
        library.add(faAngleDown, faAngleRight, faFacebookF, faTwitter);

        // This binding is necessary to make `this` work in the callback
        this.handleCloseBtn = this.handleCloseBtn.bind(this);
        this.handlerUserInfoSwitch = this.handlerUserInfoSwitch.bind(this);
    }
    handleCloseBtn = () =>{
        //this.setState( {
         //   sidebarToggleOnPrivate: !this.state.sidebarToggleOnPrivate
        //});
        this.props.changeToggleState();
    }

    handlerUserInfoSwitch(){
        this.setState({
            userInfoCollapsed:!this.state.userInfoCollapsed
        })
    }

    handleClientW = (width, num) => {
        if (width < num) {
            this.setState({
                collapsed: true
            })
        } else {
            this.setState({
                collapsed: false
            })
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize.bind(this)) //监听窗口大小改变
        let clientW = document.documentElement.clientWidth;
        this.handleClientW(clientW, 993);
    }

    handleResize = e => {
        let e_width = e.target.innerWidth;
        this.handleClientW(e_width, 993);
        console.log('浏览器窗口大小改变事件', e.target.innerWidth);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize.bind(this));
    }

    static getDerivedStateFromProps(props, state) {
        if (props.sidebarToggleOn !== state.sidebarToggleOn) {
            return {
                sidebarToggleOn: props.sidebarToggleOn,
                sidebarToggleOnPrivate: props.sidebarToggleOn?true:false
            };
        }

        return null;
    }

    render() {
        const sidebarToggleOn = this.state.sidebarToggleOnPrivate;
        const collapsed = this.state.collapsed;

        function ContextAwareToggle({children, eventKey, callback, showChild}) {
            const currentEventKey = useContext(AccordionContext);

            const decoratedOnClick = useAccordionToggle(
                eventKey,
                () => callback && callback(eventKey),
            );

            const isCurrentEventKey = currentEventKey === eventKey;
            if (showChild) {
                return (
                    <Card.Header className={styles.header} onClick={decoratedOnClick}>
                        <a className={styles.menuLink}>{children}</a>
                        <span className={styles.iconWrapper}><FontAwesomeIcon
                            icon={isCurrentEventKey ? faAngleDown : faAngleRight}/></span>
                    </Card.Header>
                );
            } else {
                return (
                    <Card.Header className={styles.header}>
                        <a className={styles.menuLink}>{children}</a>
                    </Card.Header>
                );
            }
        };

        function showSideMenuChildren(item, index) {
            if (item.content) {
                return (
                    <Accordion.Collapse eventKey={index}>
                        <div className={styles.submenuWrapper}>
                            {item.content.map((link, idx) => {
                                return <Nav.Link className={'text-uppercase'} href={link.to}>{link.label}</Nav.Link>
                            })}
                        </div>
                    </Accordion.Collapse>
                );
            }
        };

        function showSideMenu() {
            return (
                SidebarNav.map((item, index) => {
                    return (
                        <Accordion className={styles.root1} defaultActiveKey="0">
                            <Card className={styles.root}>
                                <ContextAwareToggle showChild={item.content ? true : false}
                                                    eventKey={index}>{item.label}</ContextAwareToggle>
                                {showSideMenuChildren(item, index)}
                            </Card>
                        </Accordion>
                    )
                })
            );
        };

        function showUserInfo(userInfoCollapsed){
            console.log('userInfoCollapsed:'+userInfoCollapsed);
            return (
                <div className={[userInfoCollapsed?styles.vHidden:'', styles.userInfo].join(' ')}>
                <div className="text-center p-4">
                    <FontAwesomeIcon className={styles.profileIcon} icon={faUserCircle} />
                    <p className={styles.mainText}>You Are Not Logged In!</p>
                    <p className={styles.links}><a href="#">Login</a> | <a href="#">Create New Account</a></p>
                </div>
                </div>
            );
        }
        function displaySocialBar(main) {
            const userInfoCollapsed = main.state.userInfoCollapsed;
            return (
                <div className={[styles.socialBar, 'col'].join(' ')}>
                    <div className={styles.socialWrap}>
                        <Nav className="m-auto flex-column d-xl-flex navbar-nav">
                            {SocialBarItems.map((item, idx) => {
                                return(
                                    <Nav.Item className={[link.link, styles.iconLink, item.css].join(' ')} onMouseEnter={(idx==SocialBarItems.length-1)?main.handlerUserInfoSwitch:null} onMouseLeave={(idx==SocialBarItems.length-1)?main.handlerUserInfoSwitch:null}>
                                        <Nav.Link href={item.to}><FontAwesomeIcon icon={item.icon}/></Nav.Link>
                                        {(idx==SocialBarItems.length-1)?showUserInfo(userInfoCollapsed):null}
                                    </Nav.Item>
                                )})}
                        </Nav>
                    </div>
                </div>
            );
        }

        function dispSideMenu() {

            return (
                <div className={[styles.navMenu, 'col'].join(' ')}>
                    <div className={'col'}>
                        {showSideMenu()}
                    </div>
                </div>
            );
        }

        function showSideBarInner(main) {
            //main.handleCloseBtn = main.handleCloseBtn.bind(this);
            const userInfoCollapsed = main.state.userInfoCollapsed;

            if (!collapsed) {
                return (
                    <div className={[styles.row, 'row'].join(' ')}>
                        {displaySocialBar(main)}
                        {dispSideMenu()}
                    </div>
                )
            } else {
                return (
                    <div className={[styles.row, 'row'].join(' ')}>
                        <div className={'col-12'}>
                            <div className="d-flex justify-content-center col mt-4 mb-4 col">
                                <button type="button" onClick={main.handleCloseBtn} className={['bg-transparent border-0 btn btn-primary', styles.closeBtn].join(' ')}>
                                    <FontAwesomeIcon icon={faTimesCircle} />
                                </button>
                            </div>
                            <div style={{position: 'relative'}}>
                                <div className={['m-auto', 'justify-content-center', 'flex-wrap', 'd-xl-flex', 'navbar-nav'].join(' ')}>
                                    {SocialBarItems.map((item, idx) => {
                                        return <Nav.Item className={[link.link, styles.iconLink, styles.iconLink_hz].join(' ')} onClick={(idx==SocialBarItems.length-1)?main.handlerUserInfoSwitch:null}>
                                            <Nav.Link href={item.to}><FontAwesomeIcon icon={item.icon}/></Nav.Link>
                                        </Nav.Item>
                                    })}
                                </div>
                                {showUserInfo(userInfoCollapsed)}
                            </div>
                            <div className={'col'}>
                                {showSideMenu()}
                            </div>
                            <div className={styles.menu_btm} />
                        </div>
                    </div>
                )
            }
        }

        return (
            <div
                className={[styles.sidebar_container, 'container', sidebarToggleOn ? styles.sidebar_open:''].join(' ')}>
                {showSideBarInner(this)}
            </div>

        );
    }
}

export default Sidebar;
