import React from "react";
import {Nav,Navbar,NavDropdown} from 'react-bootstrap'
import styles from './navbar.module.scss';
import styles1 from '../sidebar/sidebar.module.scss';
import logox from '../../image/logo-2x.png@120x62.webp';
import ReactDOM from 'react-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import Sidebar from '../sidebar/Sidebar';
import Dropdown from 'react-bootstrap/Dropdown';
import {SocialBarItems} from '../sidebar/SocialBarItems';
import link from "../link.module.scss";

class Mainnav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {sidebarToggleOn: false};
        // This binding is necessary to make `this  dddd` work in the  callback
        this.handleClick = this.handleClick.bind(this);
        this.handleChildClick = this.handleChildClick.bind(this);

    }

    handleClick() {
        this.setState(state => ({
            sidebarToggleOn: !state.sidebarToggleOn
        }));
      }

    handleChildClick(){
        this.setState(state => ({
            sidebarToggleOn: false
        }));
    }
    render() {
        const sidebarToggleOn = this.state.sidebarToggleOn;

        let menuBtn = null;
        if (sidebarToggleOn) {
            menuBtn = <div className={styles.rootClose}><FontAwesomeIcon icon={faPlus} /></div>;
        } else {
            menuBtn = <div className={styles.rootOpen}><span></span><span></span><span></span><span></span></div>;
        }

        return (
            <Navbar bg="dark" className={'justify-content-between'} variant="dark">
            <Navbar.Brand as="span" href="#home">
            <div class={styles.menuBtn} onClick={this.handleClick}>
                {menuBtn}
            </div>
            </Navbar.Brand>
            <Nav className={'ml-auto d-flex d-lg-none'}><img alt="" style={{width:'60px' ,height:'31px'}} src={logox} className="d-inline-block align-top" /></Nav>
            <Nav className={'mr-auto d-none d-lg-flex'} id="basic-navbar-nav">
                <Nav className={[styles.styles_navItem, styles.styles_default, 'nav-item'].join(' ')}>
                <Nav.Link className={[styles.styles_link,'d-flex align-items-center justify-content-center px-3 active'].join(' ')} href="#">Home</Nav.Link>

                <Dropdown className={[styles.styles_navItem, 'nav-item'].join(' ')} navbar={true}>
                    <Dropdown.Toggle className={[styles.styles_link,'d-flex align-items-center justify-content-center px-3 nav-link'].join(' ')} as={'a'} variant="success" id="dropdown-basic">
                        Shows
                    </Dropdown.Toggle>
                    <Dropdown.Menu className={[styles.full_widths, styles.styles_subMenu].join(' ')}>
                        <div className={[styles.styles_linksWrapper,styles.s_full_width].join(' ')}>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Show Schedule</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Breaking Point</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Ringside Politics</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Just The Truth</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>America's Voice Live</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Just The News AM</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>The Water Cooler</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>NEWS ON WITH MIRANDA KHAN</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>The Steve Gruber Show</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Live From Studio 6B</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Smack Down</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Firewall with Lance Wallnau</a>
                        <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Outside the Beltway</a>
                        </div>
                    </Dropdown.Menu>
                </Dropdown>
                <Dropdown className={[styles.styles_navItem, 'nav-item'].join(' ')} navbar={true}>
                    <Dropdown.Toggle className={[styles.styles_link,'d-flex align-items-center justify-content-center px-3 nav-link'].join(' ')} as={'a'} variant="success" id="dropdown-basic">
                        Videos
                    </Dropdown.Toggle>
                    <Dropdown.Menu className={[styles.full_widths, styles.styles_subMenu].join(' ')}>
                        <div className={[styles.styles_linksWrapper,styles.s_full_width].join(' ')}>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Recent videos</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>AVN VIDEO CHANNEL</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>ALL VIDEO CHANNELS</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Albums</a>
                        </div>
                    </Dropdown.Menu>
                </Dropdown>
                <Dropdown className={[styles.styles_navItem, 'nav-item'].join(' ')} navbar={true}>
                    <Dropdown.Toggle className={[styles.styles_link,'d-flex align-items-center justify-content-center px-3 nav-link'].join(' ')} as={'a'} variant="success" id="dropdown-basic">
                        News
                    </Dropdown.Toggle>
                    <Dropdown.Menu className={[styles.full_widths, styles.styles_subMenu].join(' ')}>
                        <div className={[styles.styles_linksWrapper,styles.s_full_width].join(' ')}>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Politics</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Congress</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Crime</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Elections</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Crime</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Immigration</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>International</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Policy</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>All news</a>
                        </div>
                    </Dropdown.Menu>
                </Dropdown>
                <Dropdown className={[styles.styles_navItem, 'nav-item'].join(' ')} navbar={true}>
                    <Dropdown.Toggle className={[styles.styles_link,'d-flex align-items-center justify-content-center px-3 nav-link'].join(' ')} as={'a'} variant="success" id="dropdown-basic">
                        Apps
                    </Dropdown.Toggle>
                    <Dropdown.Menu className={[styles.styles_default, styles.styles_subMenu].join(' ')}>
                        <div className={[styles.styles_linksWrapper,styles.styles_default].join(' ')}>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Roku</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Android</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>iPhone</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Apple TV</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Fire TV</a>
                        </div>
                    </Dropdown.Menu>
                </Dropdown>
                <Dropdown className={[styles.styles_navItem, 'nav-item'].join(' ')} navbar={true}>
                    <Dropdown.Toggle className={[styles.styles_link,'d-flex align-items-center justify-content-center px-3 nav-link'].join(' ')} as={'a'} variant="success" id="dropdown-basic">
                        Mores
                    </Dropdown.Toggle>
                    <Dropdown.Menu className={[styles.styles_default, styles.styles_subMenu].join(' ')}>
                        <div className={[styles.styles_linksWrapper,styles.styles_default].join(' ')}>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>Subscribe To Our Newsletter</a>
                            <a className={[styles.style_root, styles.styles_submenuLink].join(' ')}>About RNV</a>
                        </div>
                    </Dropdown.Menu>
                </Dropdown>
                </Nav>
            </Nav>
            <Nav className={['ml-auto d-none position-relative d-xl-flex', styles.shortcut].join(' ')}>
                {SocialBarItems.map((item, idx) => {
                    return <Nav.Item className={[styles.iconLink].join(' ')}>
                        <Nav.Link href={item.to}><FontAwesomeIcon icon={item.icon}/></Nav.Link>
                    </Nav.Item>
                })}
            </Nav>
            <Sidebar sidebarToggleOn={sidebarToggleOn} changeToggleState={this.handleChildClick} />
            </Navbar>
        );
    }
}

export default Mainnav;
